html {
  font-size: 62.5%
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', san-serif;
}
.menu-icon {
  margin-top: 3rem;
  cursor: pointer;
}

.menu-icon:hover .line{
  width: 2.5rem;
  background-color: #deaa86;
}

.line {
  height: .2rem;
  width: 2.5rem;
  background-color: #222;
  margin-bottom: .6rem;
}

.line-1, .line-3 {
  width: 1.8rem;
  transition: all 0.4s, color 0.3s;
}

.sidebar {
  width: 5rem;
  height: 100vh;
  background-color: #eee;
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  z-index: 10;
}

.social-media-list {
  list-style: none;
  padding: 0;
  height: 25rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.social-link {
  font-size: 2rem;
  color: #777;
  transition: color .3s;
}

.social-link:hover {
  font-size: 2rem;
  color: #deaa86;
}

.navigation {
  /*margin-left: 8rem; */
  left: -25em; /*(30 for navbar-8 for sidebar) */
  position: fixed;
  width: 25rem;
  height: 100vh;
  background-color: rgba(0,0,0,.7);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  transition: left .8s cubic-bezier(1,0,0,1)
}

.navigation-heading {
  font-family: 'Josefin Sans', sans-serif;
  font-size: 3.5rem;
  font-weight: 300;
  letter-spacing: .2rem;
  color: #deaa86;
  text-align: center
}

.naviation-search-input{
  width: 15rem;
  padding: 1rem 2rem;
  background-color: rgba(255,255,255,1);
  font-family: 'Josefin Sans', sans-serif;
  font-size: 1.4rem;
  color: #100;
  letter-spacing: .1rem;
  outline: none;
  border: .3rem solid #deaa86;
  border-radius: 10rem;
  transition: background-color .3s;
}

.naviation-search-input:focus {
  background-color: rgba(255,255,255,.9);
}

.navigation-search {
  position: relative;
}

.navigation-search-btn {
  border: none;
  background-color: transparent;
  color: rgba(0,0,0,0.3);
  position: absolute;
  top: 1rem;
  right: 1rem;
  cursor: pointer;
}

.navigation-list {
  list-style: none;
  padding: 0;
  height: 25rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  
}

.navigation-link {
  color: #fff;
  font-size: 1.2rem;
  font-weight: 400;
  letter-spacing: .1rem;
  text-transform: capitalize;
  text-decoration: none;
  transition: color .5s
}

.navigation-item {
  position: relative;
}

.navigation-link::before {
  content: "";
  width: 0rem;
  height: .1rem;
  background-color: #deaa86;
  position: absolute;
  top: 1rem;
  left: -4rem;
  transition: width .5s cubic-bezier(1, 0, 0, 1);
}

.navigation-link::after {
  content: "";
  width: 0rem;
  height: .1rem;
  background-color: #deaa86;
  position: absolute;
  top: 1rem;
  right: -4rem;
  transition: width .3s cubic-bezier(1, 0, 0, 1);
}

.navigation-link:hover {
  color: #deaa86;
}
.navigation-link:hover::before, .navigation-link:hover::after {
  width: 3rem;
}

.checkbox:checked ~ .navigation {
  left: 5rem;
}

.checkbox:checked ~ #container {
  margin-left: 5rem;
}

.checkbox:checked ~ .sidebar .line-1 {
  transform: rotate(-405deg) translate(-5px, 6px);
  width: 2.5rem;
}

.checkbox:checked ~ .sidebar .line-2 {
  opacity: 0
}

.checkbox:checked ~ .sidebar .line-3 {
  transform: rotate(405deg) translate(-5px, -6px);
  width: 2.5rem;
}



